import React from 'react';

// material-ui
import {
    Avatar,
    CardContent,
    CardActions,
    Card,
    Stack,
    Typography,
    Container,
    Chip,
    Grid,
    Button,
    Paper,
    TextField,
    ListItemAvatar,
    ListItemButton,
    ListItemSecondaryAction,
    Tooltip
} from '@mui/material';

import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';


// ==============================|| SAMPLE PAGE ||============================== //

const Hroffboardingadmin = () => {
    return (
        <>
            <Grid container rowSpacing={4.5} columnSpacing={2.75}>
                <Grid item xs={12}>
                    <Typography sx={{ float: 'left' }} variant="h3">HR Offboarding</Typography>
                </Grid>

                <Grid item xs={12}>
                    <TableContainer component={Paper}>
                        <Table sx={{ minWidth: 650 }} aria-label="simple table">
                            <TableHead>
                            <TableRow>
                                <TableCell>First Name</TableCell>
                                <TableCell align="center">Last Name</TableCell>
                                <TableCell align="center">Location</TableCell>
                                <TableCell align="center">Job Title</TableCell>
                                <TableCell align="center">Hire Date</TableCell>
                                <TableCell align="center">Contract End Date</TableCell>
                            </TableRow>
                            </TableHead>
                            <TableBody>

                            </TableBody>
                        </Table>
                        </TableContainer>
                </Grid>
            </Grid>
        </>
    )
}

export default Hroffboardingadmin;
